/**
 *  CENTRALIZED ICON MAPPING
 *
 * Tento modul poskytuje jednotnou logiku pro mapovn stav objednvek,
 * notifikac a dalch entit na FontAwesome ikony.
 *
 * Vechny komponenty by mly pouvat tyto funkce msto vlastnch implementac,
 * aby byla zachovna konzistence nap celou aplikac.
 */

import {
  faPlay, faHourglassHalf, faCheckCircle, faBan, faClock,
  faTruck, faShield, faFileContract, faStop, faPause,
  faTimesCircle, faArchive, faInfoCircle, faExclamationCircle,
  faBell, faExclamationTriangle, faBolt, faBullseye
} from '@fortawesome/free-solid-svg-icons';

/**
 * Mapovn stav objednvek na FontAwesome ikony
 *
 * @param {string} status - Stav objednvky (nap. 'nova', 'schvalena', 'odeslana')
 * @returns {object} FontAwesome ikona
 */
export const getStatusIcon = (status) => {
  // Normalizace: odstrann diakritiky a pevod na lowercase
  const normalizedStatus = status?.toLowerCase()
    .normalize('NFD')
    .replace(/[\u0300-\u036f]/g, ''); // Odstrann diakritiky

  switch (normalizedStatus) {
    // Zkladn stavy
    case 'nova':
      return faPlay;
    case 'ke_schvaleni':
    case 'keSchvaleni':
      return faHourglassHalf;
    case 'schvalena':
      return faCheckCircle;
    case 'zamitnuta':
      return faTimesCircle;
    case 'rozpracovana':
      return faClock;

    // Pracovn stavy
    case 'odeslana':
      return faTruck;
    case 'potvrzena':
      return faShield;
    case 'uverejnena':
    case 'registr_zverejnena':
    case 'registrzverejnena':
      return faFileContract;
    case 'dokoncena':
      return faBullseye; // Pouv se v tabulce, kompaktn reim m pmo emoji
    case 'ceka_potvrzeni':
    case 'cekaPotvrzeni':
    case 'ceka_se':
    case 'cekaSe':
      return faPause;
    case 'zrusena':
      return faTimesCircle;
    case 'archivovano':
    case 'archivovana':
      return faArchive;

    // Kontroln stavy
    case 'kontrola_ceka':
    case 'cekaKontrola':
      return faClock;
    case 'kontrola_potvrzena':
    case 'vecnaSpravnost':
    case 'vecna_spravnost':
      return faCheckCircle;

    // Vchoz ikona pro neznm stavy
    default:
      return faInfoCircle;
  }
};

/**
 * Mapovn typ notifikac na FontAwesome ikony
 *
 * Pro notifikace souvisejc s objednvkami se pouije ikona podle stavu.
 * Pro ostatn notifikace jsou definovny specifick ikony.
 *
 * @param {string} notificationType - Typ notifikace (nap. 'order_status_nova', 'todo_alarm')
 * @param {string} priority - Priorita notifikace ('low', 'normal', 'high', 'urgent')
 * @returns {object} FontAwesome ikona
 */
export const getNotificationIcon = (notificationType, priority = 'normal') => {
  // Pokud je to notifikace o zmn stavu objednvky, pouij ikonu podle stavu
  if (notificationType?.startsWith('order_status_')) {
    const status = notificationType.replace('order_status_', '');
    return getStatusIcon(status);
  }

  // Specifick typy notifikac
  switch (notificationType) {
    // TODO alarmy
    case 'todo_alarm':
      return faBell;
    case 'todo_alarm_high':
      return faExclamationTriangle;
    case 'todo_alarm_expired':
      return faExclamationCircle;

    // Force unlock
    case 'order_unlock_forced':
      return faBolt;

    // Obecn notifikace (deprecated, ale ponechno pro zptnou kompatibilitu)
    case 'order_approved':
      return faCheckCircle;
    case 'order_rejected':
      return faBan;
    case 'order_created':
      return faPlay;

    // Vchoz podle priority
    default:
      return getPriorityIcon(priority);
  }
};

/**
 * Zskn ikony podle priority notifikace
 *
 * @param {string} priority - Priorita ('low', 'normal', 'high', 'urgent')
 * @returns {object} FontAwesome ikona
 */
export const getPriorityIcon = (priority) => {
  switch (priority) {
    case 'urgent':
    case 'high':
      return faExclamationCircle;
    case 'normal':
      return faBell;
    case 'low':
    default:
      return faInfoCircle;
  }
};

/**
 * Helper funkce: Extrahuje stav objednvky z typu notifikace
 *
 * @param {string} notificationType - Typ notifikace (nap. 'order_status_schvalena')
 * @returns {string|null} Stav objednvky nebo null
 */
export const extractOrderStatusFromNotificationType = (notificationType) => {
  if (!notificationType?.startsWith('order_status_')) {
    return null;
  }
  return notificationType.replace('order_status_', '');
};

/**
 *  Mapovn typ notifikac na EMOJI ikony
 *
 * Pro notifikace souvisejc s objednvkami se pouije emoji podle stavu.
 * Pro ostatn notifikace jsou definovny specifick emoji.
 *
 * @param {string} notificationType - Typ notifikace (nap. 'order_status_nova', 'todo_alarm')
 * @param {string} priority - Priorita notifikace ('low', 'normal', 'high', 'urgent')
 * @returns {string} Emoji ikona
 */
export const getNotificationEmoji = (notificationType, priority = 'normal') => {
  // Pokud je to notifikace o zmn stavu objednvky, pouij emoji podle stavu
  if (notificationType?.startsWith('order_status_')) {
    const status = notificationType.replace('order_status_', '');
    return getStatusEmoji(status);
  }

  // Specifick typy notifikac
  switch (notificationType) {
    // TODO alarmy
    case 'todo_alarm':
      return '';
    case 'todo_alarm_high':
      return '';
    case 'todo_alarm_expired':
      return '';

    // Force unlock
    case 'order_unlock_forced':
      return '';

    // Obecn notifikace (deprecated, ale ponechno pro zptnou kompatibilitu)
    case 'order_approved':
      return '';
    case 'order_rejected':
      return '';
    case 'order_created':
      return '';

    // Vchoz podle priority
    default:
      return getPriorityEmoji(priority);
  }
};

/**
 *  Zskn emoji podle priority notifikace
 *
 * @param {string} priority - Priorita ('low', 'normal', 'high', 'urgent')
 * @returns {string} Emoji ikona
 */
export const getPriorityEmoji = (priority) => {
  switch (priority) {
    case 'urgent':
      return '';
    case 'high':
      return '';
    case 'normal':
      return '';
    case 'low':
    default:
      return '';
  }
};

/**
 *  Helper funkce pro emoji ikony stav (pro pouit v notifikacch)
 *
 * @param {string} status - Stav objednvky
 * @returns {string} Emoji ikona
 */
export const getStatusEmoji = (status) => {
  const normalizedStatus = status?.toLowerCase()
    .normalize('NFD')
    .replace(/[\u0300-\u036f]/g, ''); // Odstrann diakritiky

  switch (normalizedStatus) {
    case 'nova':
    case 'koncept':
      return '';
    case 'ke_schvaleni':
    case 'keschvaleni':
      return '';
    case 'schvalena':
      return '';
    case 'zamitnuta':
      return '';
    case 'rozpracovana':
      return '';
    case 'odeslana':
      return '';
    case 'potvrzena':
      return '';
    case 'uverejnena':
    case 'registr_zverejnena':
    case 'registrzverejnena':
      return '';
    case 'dokoncena':
      return '';
    case 'ceka_potvrzeni':
    case 'cekapotvrzeni':
      return '';
    case 'ceka_se':
    case 'cekase':
      return '';
    case 'zrusena':
      return '';
    case 'smazana':
      return '';
    case 'archivovano':
    case 'archivovana':
      return '';
    case 'ceka_kontrola':
    case 'cekakontrola':
      return '';
    case 'vecna_spravnost':
    case 'vecnaspravnost':
      return '';
    default:
      return '';
  }
};

